/** main-header **/

.main-header {
    position: relative;
    left: 0px;
    top: 0px;
    z-index: 999;
    width: 100%;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}

.sticky-header {
    position: fixed;
    opacity: 0;
    visibility: hidden;
    background: #fff;
    left: 0px;
    top: 0px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    width: 100%;
    z-index: 0;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}

.fixed-header .sticky-header {
    z-index: 999;
    opacity: 1;
    visibility: visible;
    -ms-animation-name: fadeInDown;
    -moz-animation-name: fadeInDown;
    -op-animation-name: fadeInDown;
    -webkit-animation-name: fadeInDown;
    animation-name: fadeInDown;
    -ms-animation-duration: 500ms;
    -moz-animation-duration: 500ms;
    -op-animation-duration: 500ms;
    -webkit-animation-duration: 500ms;
    animation-duration: 500ms;
    -ms-animation-timing-function: linear;
    -moz-animation-timing-function: linear;
    -op-animation-timing-function: linear;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    -ms-animation-iteration-count: 1;
    -moz-animation-iteration-count: 1;
    -op-animation-iteration-count: 1;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
}

/** header-top **/

.header-top {
    position: relative;
    padding-top: 30px;
}

.main-header.style-one .header-top .info {
    position: relative;
    display: inline-block;
    background: #ebf7f6;
    border-radius: 5px;
    padding: 4px 12px;
}

.header-top .info li {
    position: relative;
    display: inline-block;
    margin: 0px 15px;
}

.header-top .info li a {
    position: relative;
    display: inline-block;
    color: #061a3a;
}

.header-top .info li a:hover {
}

.header-top .info li.language {
    position: relative;
    cursor: pointer;
}

.header-top .info li:last-child {
    margin-right: 0px;
}

.header-top .info li.language:before {
    position: absolute;
    content: '';
    width: 30px;
    height: 30px;
    left: 0px;
    top: 3px;
}

.header-top .info li.language:after {
    position: absolute;
    top: 2px;
    right: 0px;
    content: "\f107";
    font-family: 'Font Awesome 5 Pro';
    font-size: 15px;
    font-weight: 500;
    color: #061a3a;
}

.header-top .info li.language a {
    padding-right: 13px;
}

.header-top .info li.language .language-dropdown {
    position: absolute;
    right: 0;
    top: 100%;
    margin-top: 4px;
    width: 150px;
    padding: 5px 0px;
    opacity: 0;
    visibility: hidden;
    z-index: 2;
    -webkit-transform: translateY(20px);
    -ms-transform: translateY(20px);
    transform: translateY(20px);
    -webkit-transition: all 300ms linear;
    -ms-transition: all 300ms linear;
    transition: all 300ms linear;
}

.header-top .info li.language:hover .language-dropdown {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0px);
    -ms-transform: translateY(0px);
    transform: translateY(0px);
}

.header-top .info li.language .language-dropdown li {
    position: relative;
    display: block;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    margin: 0px 15px !important;
}

.header-top .info li.language .language-dropdown li:last-child {
    border: none;
}

.header-top .info li.language .language-dropdown li a {
    position: relative;
    display: block;
    padding: 5px 0px;
    font-size: 14px;
    line-height: 24px;
    color: #fff;
    text-align: left;
}

.header-top .info li.language .language-dropdown li a:hover {
}

.main-header .logo-box .logo {
    position: relative;
    max-width: 194px;
    width: 100%;
    z-index: 1;
}

.main-header .logo-box .logo img {
    position: relative;
    width: 100%;
}

.main-header.style-one .logo-box {
    position: relative;
    padding: 19px 100px 30px 0px;
}

.main-header .logo-box .pattern {
    position: absolute;
    top: -66px;
    right: 60px;
    width: 604px;
    height: 166px;
    background-repeat: no-repeat;
    z-index: 1;
}

.main-header .logo-box .bg-color {
    position: absolute;
    top: -66px;
    right: 0px;
    width: 5000px;
    height: 166px;
    clip-path: polygon(0% 0%, 100% 0%, 99% 100%, 0% 100%, 0% 0%);
}

.main-header .btn-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-wrap: wrap;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.main-header .btn-box .theme-btn-one {
    padding: 12px 30px;
    font-size: 17px;
    font-weight: 500;
    box-shadow: 0 10px 10px #d5edea;
}

.main-header .btn-box .theme-btn-one i {
    font-size: 18px;
    margin-right: 10px;
    top: 3px;
    margin-left: 0px;
}

.main-header .outer-box {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}


/** main-menu **/

.main-menu {
    float: left;
}

.main-menu .navbar-collapse {
    padding: 0px;
    display: block !important;
}

.main-menu .navigation {
    margin: 0px;
}

.main-menu .navigation > li {
    position: inherit;
    float: left;
    z-index: 2;
    padding: 35px 0px;
    margin: 0px 3px;
    -webkit-transition: all 300ms ease;
    -moz-transition: all 300ms ease;
    -ms-transition: all 300ms ease;
    -o-transition: all 300ms ease;
    transition: all 300ms ease;
}

.main-menu .navigation > li:last-child {
    margin-right: 0px !important;
}

.main-menu .navigation > li:first-child {
    margin-left: 0px !important;
}

.main-menu .navigation > li > a {
    position: relative;
    display: block;
    text-align: center;
    font-size: 17px;
    line-height: 30px;
    font-weight: 500;
    padding: 0px 10px;
    opacity: 1;
    color: #061a3a;
    z-index: 1;
    -webkit-transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
    transition: all 500ms ease;
}

.main-menu .navigation > li.current > a,
.main-menu .navigation > li:hover > a {
}

.main-menu .navigation > li > a:before {
    position: absolute;
    content: '';
    width: 100%;
    height: 3px;
    left: 0px;
    bottom: -12px;
    transform: scale(0, 0);
    transition: all 500ms ease;
}

.main-menu .navigation > li:hover > a:before,
.main-menu .navigation > li.current > a:before {
    transform: scale(1, 1);
}

.main-menu .navigation > li.dropdown > a:after {
    position: absolute;
    top: 2px;
    right: 0px;
    content: "\f107";
    font-family: 'Font Awesome 5 Pro';
}

.main-menu .navigation > li > ul,
.main-menu .navigation > li > .megamenu {
    position: absolute;
    left: inherit;
    top: 100%;
    width: 250px;
    z-index: 100;
    display: none;
    opacity: 0;
    visibility: hidden;
    padding: 10px 0px;
    background: #061a3a;
    filter: alpha(opacity=0);
    -webkit-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    -moz-transition: all 0.2s ease-out;
    -ms-transition: all 0.2s ease-out;
    -webkit-box-shadow: 0px 4px 4px 1px rgba(0, 0, 0, 0.2);
    box-shadow: 0px 4px 4px 1px rgba(0, 0, 0, 0.2);
    -webkit-transform: rotateX(-90deg);
    transform: rotateX(-90deg);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
}

.main-menu .navigation > li > ul.from-right {
    left: auto;
    right: 0px;
}

.main-menu .navigation > li > ul > li {
    position: relative;
    width: 100%;
    padding: 0px 30px;
    -webkit-transition: all 0.2s cubic-bezier(0.4, 0.28, 0.31, 1.28) 0s;
    transition: all 0.2s cubic-bezier(0.4, 0.28, 0.31, 1.28) 0s;
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(5px);
    transform: translateY(5px);
}

.main-menu .navigation > li:hover > ul > li {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translateY(0);
    transform: translateY(0);
}

.main-menu .navigation > li > ul > li:nth-child(2n+1) {
    -webkit-transition-delay: 0.1s;
    transition-delay: 0.1s;
}

.main-menu .navigation > li > ul > li:nth-child(2n+2) {
    -webkit-transition-delay: 0.15s;
    transition-delay: 0.15s;
}

.main-menu .navigation > li > ul > li:nth-child(2n+3) {
    -webkit-transition-delay: 0.2s;
    transition-delay: 0.2s;
}

.main-menu .navigation > li > ul > li:nth-child(2n+4) {
    -webkit-transition-delay: 0.25s;
    transition-delay: 0.25s;
}

.main-menu .navigation > li > ul > li:nth-child(2n+5) {
    -webkit-transition-delay: 0.3s;
    transition-delay: 0.3s;
}

.main-menu .navigation > li > ul > li:nth-child(2n+6) {
    -webkit-transition-delay: 0.35s;
    transition-delay: 0.35s;
}

.main-menu .navigation > li > ul > li:nth-child(2n+7) {
    -webkit-transition-delay: 0.4s;
    transition-delay: 0.4s;
}

.main-menu .navigation > li > ul > li:nth-child(2n+8) {
    -webkit-transition-delay: 0.45s;
    transition-delay: 0.45s;
}

.main-menu .navigation > li > ul > li:last-child {
    border-bottom: none;
}

.main-menu .navigation > li > ul > li > a,
.main-menu .navigation > li > .megamenu li > a {
    position: relative;
    display: block;
    padding: 14px 0px;
    line-height: 24px;
    font-weight: 400;
    font-size: 16px;
    text-transform: capitalize;
    color: #fff;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    text-align: left;
    transition: all 500ms ease;
    -moz-transition: all 500ms ease;
    -webkit-transition: all 500ms ease;
    -ms-transition: all 500ms ease;
    -o-transition: all 500ms ease;
}

.main-menu .navigation > li > ul > li > a:hover,
.main-menu .navigation > li > .megamenu li > a:hover {
}

.main-menu .navigation > li > ul > li:last-child > a,
.main-menu .navigation > li > .megamenu li:last-child > a {
    border-bottom: none;
}

.main-menu .navigation > li > ul > li > a:hover {
}

.main-menu .navigation > li > ul > li.dropdown > a:after {
    font-family: 'Font Awesome 5 Pro';
    content: "\f105";
    position: absolute;
    right: 0px;
    top: 14px;
    display: block;
    line-height: 24px;
    font-size: 16px;
    font-weight: 800;
    text-align: center;
    z-index: 5;
}

.main-menu .navigation > li > ul > li > ul {
    position: absolute;
    right: 100%;
    top: 0%;
    width: 230px;
    z-index: 100;
    display: none;
    padding: 10px 0px;
    background: #061a3a;
    filter: alpha(opacity=0);
